<template>
    <transition name="block">
        <div class="nested-modal-mask">
            <div class="modal-wrapper">
                <div class="modal-container">
                    <modal-header
                            :data="getNestedModal.selectedFolder"
                            modalType="nestedModal"/>
                    <div class="modal-body">
                        <div class="modal-body__container">
                            <file v-for="file in getNestedModal.selectedFolder.children.filter(val => val.type === 'file')"
                                  :file="file"
                                  :key="file.id"/>
                            <folder v-for="folder in getNestedModal.selectedFolder.children.filter(val => val.type === 'folder')"
                                    :folder="folder"
                                    :key="folder.id"
                                    folderType="childNestedFolder"/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
  import { mapGetters } from 'vuex'
  import File from "./File";
  import Folder from "./Folder";
  import ModalHeader from "./ModalHeader";
  const closeIcon = require('../assets/close-icon.svg');
  const fullScreenIcon = require('../assets/full-screen-icon.svg');
  export default {
    name: "NestedModal",
    components: {ModalHeader, Folder, File},
    computed:{
      ...mapGetters([
        'getNestedModal'
      ])
    },
    data(){
      return{
        closeIcon,
        fullScreenIcon
      }
    }
  }
</script>

<style scoped lang="less">
    .block-enter {
        opacity: 0;
        transition : opacity 0.5s;
    }
    .block-enter-active {
        transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    .block-leave-active {
        transition: opacity 0.5s;
        opacity: 0;
    }
    .nested-modal-mask {
        position: absolute;
        z-index: 9998;
        top: 34px;
        left: 34px;
        max-width: 618px;
        width:100%;
        box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
        box-sizing: border-box;
        border-radius: 3px;
        animation: expand 0.5s;
    }
    @-webkit-keyframes expand{
        0% {
            width:0;
        }
        100% {
            width:100%;
        }
    }
    .modal-container {
        background-color: #fff;
        border-radius: 3px 3px 0 0;
        overflow:hidden;
        transition: all 0.3s ease;
    }
    .modal-body {
        display:flex;
        height: 753px;
        justify-content: center;
        &__container{
            display: grid;
            grid-template-columns: 178px 178px 178px;
            grid-template-rows: 214px 214px;
            grid-column-gap: 8px;
            grid-row-gap: 8px;
        }
    }

</style>
